
import Toast from 'vux/src/components/toast/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'


export default {
  components: {
    XInput,
    XButton,
    Group,
    Toast
  },
  props:{
    logincolor:'',
  },
  data () {
    return {
      phone:'',
      password:'',
      smstext:'发送验证码',
      btn_type:'primary',
      iconType: '',
      focusStatu:false,
      timer: 60,       //默认倒数30秒
      stop : false,   //默认是停止的，但界面加载之后会变成false
      Interval:null,  //setInterval的对象
      disabled: false,
      style: '',
      Interval:null,
      showPositionValue: false,
      login_type:'default',
      disabled_login:true

    }
  },
  methods: {
    allow_login() {
      this.login_type = 'primary';
      this.disabled_login=false;
    },
    update () {
      if(this.timer <= 0) 
      {
          this.timer = 60;
      }
      else{
          this.timer--;
          this.smstext= this.timer + "s重新获取";
          this.disabled=true;
          this.btn_type="default";
      }
    },
    send_sms () {
      //如果是false就开始倒计时，如果是true就停止倒计时
      if(this.stop == false) 
      {
          this.showPositionValue = true;
          this.Interval = setInterval(this.update,1000); 
      }
      else
      {
          clearInterval(this.Interval);
      }

      this.stop = !this.stop;
    }
  }
}



